#page-converter{
    background: url("../img/background.jpg");
    background-size: cover;
}

#page-converter .converter-background-opacity{
    background: rgba(0, 0, 0, 0.8);
}

#page-converter .converter-frame{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
}

#page-converter .title{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}

#page-converter .title h1{
    color: #a0ff61;
    font-size: 30px;
}

#page-converter .title svg{
    margin-right: 5px;
    color: #a0ff61;
    width: 30px;
    height: 30px;
}

#page-converter .converter-frame .converter-content{
    width: 40%;
    min-width: 400px;
    max-width: 500px;
    display: flex;
    flex-direction: column;
}

#page-converter .converter-frame .converter-content .from-to{
    display: flex;
}

#page-converter .converter-frame .converter-content .from-to select{
    margin: 0 12px;
    padding: 4px 8px;
    outline: none;
    border: none;
    border-radius: 10px;
    font-weight: bold;
}

#page-converter .converter-frame .converter-content .from-to h1{
    color: white;
}

#page-converter .converter-frame a{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 18px;
    border: none;
    border-radius: 12px;
    color: black;
    background-color: #a0ff61;
    cursor: pointer;
    outline: none;
    margin-top: 40px;
    transition: 400ms;
    text-decoration: none;
}

#page-converter .converter-frame a:hover{
    color: white;
    background-color: #66a13e;
}

#page-converter .converter-frame a svg{
    margin-left: 5px;
}

#page-converter .converter-frame .converter-content .amount{
    margin-bottom: 20px;
    width: 100%;
}

#page-converter .converter-frame .converter-content .amount h1{
    color: white;
}

#page-converter .converter-frame .converter-content .amount input{
    width: 100%;
    padding: 8px 5px;
    outline: none;
    font-weight: bold;
}

.output-frame{
    margin-top: 20px;
    color: #a0ff61;
    text-align: center;
}

.output-frame #output{
    font-size: 19px;
}