#page-main{
    background: url("../img/background.jpg");
    background-size: cover;
}

#page-main .main-background-opacity{
    background: rgba(0, 0, 0, 0.8);
}

#page-main .main-content{
    display: flex;
    width: 80%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

#page-main .main-content .title{
    display: flex;
    align-items: center;
    justify-content: center;
}

#page-main .main-content a{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px 18px;
    border: none;
    border-radius: 12px;
    color: black;
    background-color: #a0ff61;
    cursor: pointer;
    outline: none;
    margin-top: 24px;
    transition: 400ms;
}

#page-main .main-content a:hover{
    background-color: #66a13e;
    color: white;
}

#page-main .main-content a svg{
    margin-left: 5px;
}

#page-main .main-content .title svg{
    color: #a0ff61;
    width: 80px;
    height: 80px;
    margin-right: 10px;
}

#page-main .main-content h1{
    font-size: 80px;
    color: #a0ff61;
}

#page-main .main-content h2{
    color: white;
}

#page-main .modal{
    z-index: 20;
    top: 0;
    display: flex;
    flex-direction: column;
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.7);
    align-items: center;
    justify-content: center;
}

#page-main .modal h1, #page-main .modal h2{
    color: white;
    line-height: 1;
}

#page-main .modal svg{
    position: absolute;
    top: 5px;
    right: 5px;
    color: white;
    cursor: pointer;
    width: 45px;
    height: 45px;
}

#page-main .modal .modal-frame{
    display: flex;
    flex-direction: column;
    width: 61%;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    justify-content: center;
}

#page-main .modal .modal-frame .modal-content h1{
    margin-bottom: 10px;
}

#page-main .modal .modal-frame .modal-content a{
    margin-top: 20px;
    padding: 8px 18px;
    border: none;
    border-radius: 12px;
    color: black;
    background-color: #a0ff61;
    cursor: pointer;
    outline: none;
    transition: 400ms;
    display: inline-block;
    font-family: Hind;
    text-decoration: none;
}

#page-main .modal .modal-frame .modal-content a:hover{
    background-color: #66a13e;
    color: white;
}

#page-main .modal.hidden{
    display: none;
}

#page-main .main-bottom-text{
    position: fixed;
    left: 10px;
    bottom: 10px;
    color: white;
    display: flex;
}

#page-main .main-bottom-text a{
    color: #a0ff61;
}

#page-main .main-frame{
    display: flex;
    align-items: center;
    justify-content: center;
}

@media (max-width: 700px){
    #page-main .main-content h1{
        font-size: 50px;
    }
    
    #page-main .main-content h2{
        font-size: 16px;
    }

    #page-main .main-content .title svg{
        color: #a0ff61;
        width: 40px;
        height: 40px;
        margin-right: 10px;
    }

    #page-main .main-bottom-text{
        text-align: center;
    }
}